@import url("augmentedsteam.css");

.steamdb_ico i {
  background-image: url("chrome-extension://__MSG_@@extension_id__/img/steamdb_store.png");
}
.steamdb_ico:hover i {
  background-image: url("chrome-extension://__MSG_@@extension_id__/img/steamdb_store_black.png");
}

.itad_ico i {
  background-image: url("chrome-extension://__MSG_@@extension_id__/img/itad_small.png");
}
.itad_ico:hover i {
  background-image: url("chrome-extension://__MSG_@@extension_id__/img/itad_small_black.png");
}

.bartervg_ico i {
  background-image: url("chrome-extension://__MSG_@@extension_id__/img/bartervg.png");
}

.as_youtube_btn i {
  background-image: url("chrome-extension://__MSG_@@extension_id__/img/icon-youtube.png");
}

.twitch_btn i {
  background-image: url("chrome-extension://__MSG_@@extension_id__/img/icon-twitch.png");
}

.pcgw_btn i {
  background-image: url("chrome-extension://__MSG_@@extension_id__/img/pcgw.png");
}

.completionistme_btn i {
  background-image: url("chrome-extension://__MSG_@@extension_id__/img/icon-completionistme.png");
}

.protondb_btn i {
  background-image: url("chrome-extension://__MSG_@@extension_id__/img/icon-protondb.png"); /* //www.protondb.com/images/protondb-logo.svg */
}

.cardexchange_btn i {
  background-image: url("chrome-extension://__MSG_@@extension_id__/img/steamcardexchange.png");
}

.es_slider_toggle i,
.es_screenshot_fullscreen_toggle i {
  background-image: url("chrome-extension://__MSG_@@extension_id__/img/fullscreen-icons.gif");
}

.es_screenshot_open_btn i {
  background-image: url('chrome-extension://__MSG_@@extension_id__/img/screenshot_open.png');
}

.es_regional_icon {
  background-image: url("chrome-extension://__MSG_@@extension_id__/img/flags/world.png") !important;
}

.itad-pricing {
  background-image: url("chrome-extension://__MSG_@@extension_id__/img/itad.png");
}

.es_external_icon::after {
  content: " " url("chrome-extension://__MSG_@@extension_id__/img/ico_external_link.gif"); /* https://store.steampowered.com/public/images/v5/ico_external_link.gif */
  border: 0;
  vertical-align: baseline;
}

.es_sites_icons.es_steamrepcn_icon:before {
  background-image: url("chrome-extension://__MSG_@@extension_id__/img/ico/srcn.png");
}
.es_sites_icons.es_steamrep_icon:before {
  background-image: url("chrome-extension://__MSG_@@extension_id__/img/ico/steamrep.png");
  background-color: rgb(235, 235, 235);
}
.es_sites_icons.es_steamdbcalc_icon:before {
  background-image: url("chrome-extension://__MSG_@@extension_id__/img/ico/steamdb.png");
}
.es_sites_icons.es_steamgifts_icon:before {
  background-image: url("chrome-extension://__MSG_@@extension_id__/img/ico/steamgifts.png");
}
.es_sites_icons.es_steamtrades_icon:before {
  background-image: url("chrome-extension://__MSG_@@extension_id__/img/ico/steamtrades.png");
}
.es_sites_icons.es_bartervg_icon:before {
  background-image: url("chrome-extension://__MSG_@@extension_id__/img/ico/bartervg.png");
}
.es_sites_icons.es_astats_icon:before {
  background-image: url("chrome-extension://__MSG_@@extension_id__/img/ico/achievementstats.png");
}
.es_sites_icons.es_backpacktf_icon:before {
  background-image: url("chrome-extension://__MSG_@@extension_id__/img/ico/backpacktf.png");
}
.es_sites_icons.es_astatsnl_icon:before {
  background-image: url("chrome-extension://__MSG_@@extension_id__/img/ico/astatsnl.png");
}

.es-flag:before {
  background-image: url("chrome-extension://__MSG_@@extension_id__/img/flags/flags.png");
}

/* || DOUBLE SLIDER */

/* Displays the lower stacked thumb above higher stacked track */
.as-double-slider__input::-webkit-slider-thumb {
  position: relative;
}
